import postRobot from '../../../post-robot-wrapper';
import './konbini.html';
import '../styles/defaults.css';
import { handleEvent, runEventOnElement } from '../input-events';
import { applyOptions } from '../options';
import { getComponentIdFromQueryString, getControllerIdFromQueryString } from '../querystring';
import {config} from '../config';
import {
  mountComponent,
  addTriggerEvent,
  addGetComponentData,
  addHandleOptions
} from '../payment-events';
import {generateComponentData} from '../payment-component-data';
import {getElementHeight, setSelectStyles} from '../../../client/css-class-utils';
import {getLocaleMessage} from '../localization/localized-messages';
import customSelect from 'custom-select';

const COMPONENT_TYPE = 'konbini';
let storesSelect;

let componentData = generateComponentData(COMPONENT_TYPE,getComponentIdFromQueryString(COMPONENT_TYPE), getControllerIdFromQueryString());

const defaultOptions = {
  placeholderText: getLocaleMessage('en-US', 'konbiniSelectStore')
};

const controllerListener = postRobot.listener({ window: componentData.controller.window, domain: config.domain });
const controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });

const triggerData = {
  componentData: componentData,
  getElement: getElement,
  getFocusElement: getFocusElement
};


mountComponent(controllerEmitter, componentData, handleOptions, emitComponentReady);
addHandleOptions(controllerListener, handleOptions);
addGetComponentData(controllerListener, getElement);
addTriggerEvent(controllerListener, runEventOnElement, triggerData);

/**
 * getElement returns the online banking dom element
 * @returns {HTMLElement}
 */
export function getElement() {
  return document.getElementById('konbini');
}

/**
 * getFocusElement returns the online banking dom element for focus
 * @returns {HTMLElement}
 */
export function getFocusElement() {
  return storesSelect.opener;
}

function loadSelectStoresData(stores, placeholderText, locale) {

  // first set defaults
  let selectedPlaceholderText = defaultOptions.placeholderText;

  // then look for locale overrides
  if (typeof getLocaleMessage(locale, 'konbiniSelectStore') !== 'undefined') {
    selectedPlaceholderText = getLocaleMessage(locale, 'konbiniSelectStore');
  }

  // placeholderText specified by client should override all
  if (typeof placeholderText !== 'undefined' && placeholderText !== null) {
    selectedPlaceholderText = placeholderText;
  }

  if (typeof stores === 'undefined' || stores.length === 0) {
    selectedPlaceholderText = getLocaleMessage(locale, 'konbiniSelectStore');
    stores = [];
  }

  const select = getElement();

  if(select.querySelectorAll('option').length > 0) {
    select.innerHTML = '';
  }
  const option = document.createElement('option');
  option.value = '';
  option.text = selectedPlaceholderText;
  select.add(option);

  stores.forEach(store => {
    const option = document.createElement('option');
    option.value = store.storeId;
    option.text = store.localizedStoreName;
    select.add(option);
  });
}

/**
 * handleOptions applies data to the ccNumber dom element
 * @param {object} data
 */
export function handleOptions(data) {

  let el = getElement();

  const stores = data.stores;

  applyOptions(el, data.options, defaultOptions);

  const placeholderText = data.options.placeholderText;
  const locale = data.instanceOptions && data.instanceOptions.hasOwnProperty('locale') ? data.instanceOptions.locale : null;

  loadSelectStoresData(stores, placeholderText, locale);
  customizeSelect(el, data.options.style, stores);
  setSelectStyles('customStyles', data.options);
  componentData.hasStores = stores && stores.length > 0;
  addInstanceOptions(data.instanceOptions);
}

/**
 * Stores the instance options in the component state
 * @param instanceOptions
 */
export function addInstanceOptions(instanceOptions) {
  componentData.instanceOptions = instanceOptions;
}


export function customizeSelect(element, styleOptions, stores) {
  if(document.querySelector('.custom-select-container')) {
    document.querySelector('.custom-select-container').customSelect.destroy();
  }

  storesSelect = customSelect(element)[0];

  stores.forEach(store => {
    const logoUrl = store.storeLogoUrl;
    const storeId = store.storeId;

    const logo = document.createElement('img');
    logo.setAttribute('src', logoUrl);
    logo.setAttribute('alt', store.localizedStoreName);

    const option = document.querySelector(`[data-value="${storeId}"]`);

    option.insertBefore(logo, option.firstChild);
  });

  const collapsedHeight = getElementHeight(styleOptions);
  resizeIframe(collapsedHeight);
  storesSelect.container.classList.add('base');
  storesSelect.container.classList.add('empty');
  storesSelect.container.addEventListener('custom-select:open', function() {
    let newSize = `calc(${window.getComputedStyle(storesSelect.panel).height} + ${collapsedHeight}`;
    resizeIframe(newSize);
  });
  storesSelect.container.addEventListener('custom-select:close', function() {resizeIframe(collapsedHeight);});
  storesSelect.select.addEventListener('change', (event) => {

    // once user has selected a bank, remove placeholder option if it has not already been removed
    if(event.trigger !== 'showError' && storesSelect.select.options[0].value === '') {
      storesSelect.remove(storesSelect.select.options[0]);
    }
    //removing classes in 3 lines instead of one because IE11 won't support all in one line
    storesSelect.container.classList.remove('complete');
    storesSelect.container.classList.remove('invalid');
    storesSelect.container.classList.remove('empty');
    storesSelect.container.setAttribute('class', element.getAttribute('class') + ' ' + storesSelect.container.getAttribute('class'));
  });
  storesSelect.opener.addEventListener('focus', (e) => {
    handleFocus(e);
  });
  storesSelect.opener.addEventListener('blur', (e) => {
    let event = {
      isTrusted: e.isTrusted,
      type: 'blur',
      srcElement: storesSelect.container,
      target: document.getElementById('konbini')
    };
    handleBlur(event);
  });
  window.addEventListener('keydown', (e) => {
    if(e.key === 'Tab' && storesSelect.open) {
      resizeIframe(collapsedHeight);
      storesSelect.open = false;
    }
  });
  window.addEventListener('blur', () => {
    resizeIframe(collapsedHeight);
    storesSelect.open = false;
  });

  document.addEventListener('focus', function() {storesSelect.opener.focus();}, false);

}

export function resizeIframe(newSize) {
  let eventData = {
    frame: {
      height: newSize,
      id: componentData.componentId
    }
  };
  let event = new CustomEvent('resize', { detail: eventData });
  handleEvent(componentData, 'resize', event);
}


/**
 * handleBlur accepts a blur event and sends it on to handleEvent
 * @param {event} event
 */
export function handleBlur(event) {
  handleEvent(componentData, 'blur', event);
}

/**
 * handleFocus accepts a focus event and sends it on to handleEvent
 * @param {event} event
 */
export function handleFocus(event) {
  handleEvent(componentData, 'focus', event);
}

/**
 * handleChange accepts a change event
 * @param {event} event
 */
export function handleChange(event) {
  handleEvent(componentData, 'change', event);
}

/**
 * handleAutofill accepts an autofill event and sends it on to handleEvent
 * @param {event} event
 */
export function handleAutofill(event) {
  handleEvent(componentData, 'autofill', event);
}

/**
 * emitComponentReady calls handleEvent with 'ready'
 */
export function emitComponentReady() {
  let event = new CustomEvent('ready', { detail: {hasAvailableStores: componentData.hasStores} });
  handleEvent(componentData, 'ready', event);
}

export function addInputHtmlToDom() {
  document.body.innerHTML += inputHtml();
}

export function inputHtml() {
  return `<select id="konbini"
         onfocus="handleFocus(event)"
         onblur="handleBlur(event)"
         onchange="handleChange(event)"
  />`;
}
